import React from 'react';
import classnames from 'classnames';
import { list } from '../components';
import styles from './index.less';

export const IS_DEV = !!window.location.port;

const view = ({
  // showType = 'last',
  // showType = 'list',
  showType = IS_DEV ? 'last' : 'random'
}: any) => {
  let demos: any[] = [];
  if (showType === 'last') {
    demos = [list[list.length - 1]];
  } else if (showType === 'random') {
    demos = [list[Math.floor(Math.random() * list.length)]];
  } else if (showType === 'first') {
    demos = [list[0]];
  } else {
    const item = list.find(i => i.id === showType);
    demos = item ? [item] : list;
  }
  return (
    <div className={classnames(styles.list, demos.length === 1 && styles.solo)}>
      {demos.map(i => (
        <div key={i.id} className={styles.item}>
          <div className={styles.view}>
            {i.item}
          </div>
        </div>
      ))}
    </div>
  );
};

export default view;
