import React from 'react';
import { Canvas } from '@react-three/fiber';
import { useGLTF, OrbitControls, ContactShadows } from '@react-three/drei';
import { useControls } from 'leva';
import { isInMicroApp } from '@/utils/microApp';

const models: any = {
  Beech: 'https://vazxmixjsiawhamofees.supabase.co/storage/v1/object/public/models/tree-beech/model.gltf',
  Lime: 'https://vazxmixjsiawhamofees.supabase.co/storage/v1/object/public/models/tree-lime/model.gltf',
  Spruce: 'https://vazxmixjsiawhamofees.supabase.co/storage/v1/object/public/models/tree-spruce/model.gltf'
};

// 预加载
Object.values(models).forEach(useGLTF.preload);

export default function App() {
  const { model } = isInMicroApp ? { model: 'Beech' } : useControls({ model: { value: 'Beech', options: Object.keys(models) } });
  const { scene }: any = useGLTF(models[model]);
  return (
    <Canvas camera={{ position: [-10, 10, 40], fov: 50 }}>
      <hemisphereLight color="#fff" groundColor="#aaa" intensity={0.75} />
      <spotLight position={[50, 50, 10]} angle={0.25} penumbra={1} />
      <group position={[0, -10, 0]}>
        <primitive position={[0, 0.25, 0]} object={scene} url={models[model]} />
        <ContactShadows scale={20} blur={10} far={20} />
      </group>
      <OrbitControls />
    </Canvas>
  );
};
